// Core
import { Link } from 'react-router-dom';

// Others
import { RootState } from '../../store/store';
import { selectAuthStatus } from '../../store/features/auth/auth-selector';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { selectFilmById } from '../../store/features/films/films-selector';
import { FilmsSectionProps } from './film-section.props';
import { Film } from '../../types/films';

// Components
import { Header } from '../header/header';
import { Logo } from '../logo/logo';
import { UserBlock } from '../user-block/user-block';
import { PlayButton } from '../play-button/play-button';
import { MyListButton } from '../my-list-button/my-list-button';
import { Poster } from '../poster/poster';
import { Tab } from '../tab/tab';

// Component
export const FilmSection = ({
  filmId
}: FilmsSectionProps): JSX.Element => {
  const isAuthorized = useAppSelector(selectAuthStatus);

  const movie = useAppSelector(
    (state: RootState) => selectFilmById(state, filmId.toString()) as Film
  );

  return (
    <section style={{ backgroundColor: movie.backgroundColor }} className="film-card film-card--full">
      <div className="film-card__hero">
        <div className="film-card__bg">
          <img src={movie.backgroundImage} alt="" />
        </div>

        <h1 className="visually-hidden">WTW</h1>

        <Header className="film-card__head">
          <Logo />
          <UserBlock />
        </Header>

        <div className="film-card__wrap">
          <div className="film-card__desc">
            <h2 className="film-card__title">{movie.name}</h2>
            <p className="film-card__meta">
              <span className="film-card__genre">{movie.genre}</span>
              <span className="film-card__year">{movie.released}</span>
            </p>

            <div className="film-card__buttons">
              <PlayButton filmId={movie.id} />
              {isAuthorized && (
                <>
                  <MyListButton
                    filmId={filmId}
                    isFavorite={movie.isFavorite}
                  />
                  <Link to={`/films/${filmId}/review`} className="btn film-card__button">Add review</Link>
                </>
              )}
            </div>
          </div>
        </div>
      </div>

      <div className="film-card__wrap film-card__translate-top">
        <div className="film-card__info">
          <Poster
            posterImage={movie.posterImage} className='film-card__poster--big'
          />

          <Tab filmId={movie.id} />
        </div>
      </div>
    </section>
  );
};
