// Core
import { memo, MouseEvent } from 'react';
import { Link } from 'react-router-dom';
import { useAppDispatch } from '../../hooks/use-app-dispatch/use-app-dispatch';

// Others
import { AppRoute } from '../../consts';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { logoutActionAsync } from '../../store/features/auth/auth-api';
import { selectAuthStatus } from '../../store/features/auth/auth-selector';
import { selectProfileData } from '../../store/features/profile/profile-selector';

// Component
export const UserBlock = memo(() => {
  const authStatus = useAppSelector(selectAuthStatus);
  const { avatarUrl } = useAppSelector(selectProfileData);

  const dispatch = useAppDispatch();

  const handleLogoutClick = (event: MouseEvent<HTMLAnchorElement>) => {
    event.preventDefault();
    dispatch(logoutActionAsync());
  };

  if (authStatus) {
    return (
      <ul className="user-block">
        <li className="user-block__item">
          <div className="user-block__avatar">
            <Link to={AppRoute.Favorites}>
              <img src={avatarUrl} alt="User avatar" width="63" height="63" />
            </Link>
          </div>
        </li>
        <li className="user-block__item">
          <Link
            to={AppRoute.Root}
            onClick={handleLogoutClick}
            className="user-block__link"
          >
            Sign out
          </Link>
        </li>
      </ul>
    );
  } else {
    return (
      <ul className="user-block">
        <li className="user-block__item">
          <Link to={AppRoute.Login} className="user-block__link">Sign in</Link>
        </li>
      </ul>
    );
  }
});

UserBlock.displayName = 'UserBlock<Memo>';
