// Others
import { Genres, NameSpace } from '../../../consts';
import { Film } from '../../../types/films';
import { RootState } from '../../store';

// Selectors

export const selectFilmsByGenre = (state: RootState) => {
  const genre = state[NameSpace.Genre].selectedGenre;
  const films = state[NameSpace.Films].entities;

  if (genre === Genres.AllGenres) {
    return films;
  }

  return films.filter((film) => film.genre === genre);
};

export const selectFilmStatus = (state: RootState) => state[NameSpace.Films].status;

export const selectFilmById = (state: RootState, id: string): Film | undefined =>
  state[NameSpace.Films].entities
    .find((it) => it.id === parseInt(id, 10));

export const movieByIdExists = (state: RootState, id: number) => {
  const movie = state[NameSpace.Films].entities
    .find((film) => film.id === id);

  return !movie;
};
