// Others
import { TabCommentsProps } from './tab-comments.props';
import { ReviewCard } from '../review-card/review-card';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { selectIsErrorReviews } from '../../store/features/reviews/reviews-selector';

function isEven(value: number) {
  return (value % 2 === 0);
}

// Component
export const TabComments = (props: TabCommentsProps): JSX.Element => {
  const { reviews } = props;

  const isError = useAppSelector(selectIsErrorReviews);

  if (reviews.length === 0) {
    return (
      <div className="film-card__reviews">
        <p className="film-card__text">There are no reviews yet.</p>
      </div>
    );
  }

  if (isError) {
    return (
      <div className="film-card__reviews">
        <p className="film-card__text">Failed to get list of reviews.</p>
      </div>
    );
  }

  return (
    <div className="film-card__reviews film-card__row">
      <div className="film-card__reviews-col">
        {reviews.map((review, idx) => isEven(idx) && (
          <ReviewCard
            key={review.id}
            review={{
              comment: review.comment,
              user: review.user,
              date: review.date,
              rating: review.rating,
            }}
          />
        ))}
      </div>
      <div className="film-card__reviews-col">
        {reviews.map((review, idx) => !isEven(idx) && (
          <ReviewCard
            key={review.id}
            review={{
              comment: review.comment,
              user: review.user,
              date: review.date,
              rating: review.rating,
            }}
          />
        ))}
      </div>
    </div>
  );
};
