// Others
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { selectFilmsByGenre, selectFilmStatus } from '../../store/features/films/films-selector';
import { FilmsList } from '../../components/films-list/films-list';
import { GenresList } from '../../components/genres-list/genre-list';
import { Spinner } from '../spinner/spinner';
import { useShowMore } from '../../hooks/use-show-more/use-show-more';
import { MAX_SHOW_FILMS, RequestsStatus } from '../../consts';

export const Catalog = (): JSX.Element => {
  const films = useAppSelector(selectFilmsByGenre);
  const filmsStatus = useAppSelector(selectFilmStatus);

  const {
    maxShowFilms,
    showMoreToDisplay,
    onShowMoreButton,
  } = useShowMore(MAX_SHOW_FILMS, films.length);

  const filteredFilms = films.slice(0, maxShowFilms);

  if (filmsStatus === RequestsStatus.Loading) {
    return (
      <section className="catalog">
        <GenresList />
        <Spinner />
      </section>
    );
  }

  return (
    <section className="catalog">
      <h2 className="catalog__title visually-hidden">Catalog</h2>

      <GenresList />
      <FilmsList films={filteredFilms} />

      {showMoreToDisplay && (
        <div className="catalog__more">
          <button
            onClick={onShowMoreButton}
            className="catalog__button"
            type="button"
          >
            Show more
          </button>
        </div>
      )}
    </section>
  );
};
