// Core
import { useEffect, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

// Others
import { VideoPlayerSpinner } from '../../components/video-player-spinner/video-player-spinner';
import { VideoProgress } from '../../components/video-progress/video-progress';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { useFullScreen } from '../../hooks/use-full-screen/use-full-screen';
import { selectFilmById } from '../../store/features/films/films-selector';
import { RootState } from '../../store/store';
import { NotFoundPage } from '../not-found-page/not-found-page';

// Component
export const PlayerPage = (): JSX.Element => {
  // Router
  const params = useParams();
  const navigate = useNavigate();

  //  Refs
  const videoRef = useRef<HTMLVideoElement>(null);

  // States
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [isPlaying, setIsPlaying] = useState<boolean>(false);

  // Effects
  // ----------------------------------------------------------------

  useEffect(() => {
    const videoPlayer = videoRef.current;

    if (videoPlayer) {
      videoPlayer.onplaying = () => setIsLoading(false);
      videoPlayer.onwaiting = () => setIsLoading(true);
    }
    return () => {
      if (videoPlayer) {
        videoPlayer.onplaying = null;
        videoPlayer.onwaiting = null;
      }
    };
  }, []);

  // Custom
  // ----------------------------------------------------------------
  const onToggleFullScreen = useFullScreen();

  // Selectors
  const film = useAppSelector(
    (state: RootState) => selectFilmById(state, params.id ?? '-1')
  );


  if (!film) {
    return <NotFoundPage />;
  }

  // Handlers
  // ----------------------------------------------------------------

  const handleToggleVideoButtonClick = () => {
    const videoPlayer = videoRef.current;

    if (videoPlayer) {
      if (isPlaying) {
        videoPlayer.pause();
        setIsPlaying(false);
      } else {
        videoPlayer.play();
        setIsPlaying(true);
      }
    }
  };

  // Render
  // ----------------------------------------------------------------

  const { name, videoLink, previewImage } = film;

  return (
    <div
      className="player"
      style={{ backgroundColor: 'black' }}
    >

      {isPlaying && isLoading && (
        <VideoPlayerSpinner />
      )}

      <video
        ref={videoRef}
        src={videoLink}
        className="player__video"
        poster={previewImage}
        style={{ objectFit: 'contain' }}
      />

      <button
        type="button"
        className="player__exit"
        onClick={() => navigate(-1)}
      >
        Exit
      </button>

      <div className="player__controls">
        <VideoProgress videoRef={videoRef} />

        <div className="player__controls-row">
          <button onClick={handleToggleVideoButtonClick} type="button" className="player__play">
            <svg viewBox="0 0 19 19" width="19" height="19">
              <use xlinkHref={isPlaying ? '#pause' : '#play-s'} />
            </svg>

            <span>{isPlaying ? 'Pause' : 'Play'}</span>
          </button>

          <div className="player__name">{name}</div>

          <button onClick={onToggleFullScreen} type="button" className="player__full-screen">
            <svg viewBox="0 0 27 27" width="27" height="27">
              <use xlinkHref="#full-screen" />
            </svg>

            <span>Full screen</span>
          </button>
        </div>
      </div>
    </div>
  );
};
