// Core
import { memo, MouseEvent } from 'react';
import cn from 'classnames';
import { Link } from 'react-router-dom';

// Others
import { Genres } from '../../consts';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { useAppDispatch } from '../../hooks/use-app-dispatch/use-app-dispatch';
import { changeSelectedGenre } from '../../store/features/genres/genres-slice';
import { selectGenre } from '../../store/features/genres/fenres-selectors';

// Component
export const GenresList = memo((): JSX.Element => {
  const selectedGenre = useAppSelector(selectGenre);
  const dispatch = useAppDispatch();

  const handleChangeSelectedGenre = (
    event: MouseEvent<HTMLSpanElement>,
    genre: Genres
  ) => {
    event.preventDefault();
    dispatch(changeSelectedGenre(genre));
  };

  return (
    <ul className="catalog__genres-list">
      {Object.values(Genres).map((genre) => (
        <li
          key={`genre-${genre}`}
          className={cn('catalog__genres-item', {
            'catalog__genres-item--active': selectedGenre === genre,
          })}
        >
          <Link
            to={`#${genre}`}
            className="catalog__genres-link"
            onClick={(e) => handleChangeSelectedGenre(e, genre)}
          >
            {genre}
          </Link>
        </li>
      ))}
    </ul>
  );
});

GenresList.displayName = 'GenreList<Memo>';
