// Core
import { combineReducers } from '@reduxjs/toolkit';

// Others
import { NameSpace } from '../consts';

// Reducers
import appReducer from './features/app/app-slice';
import filmsReducer from './features/films/films-slice';
import genreReducer from './features/genres/genres-slice';
import profileReducer from './features/profile/profile-slice';
import authReducer from './features/auth/auth-slice';
import promoReducer from './features/promo/promo-slice';
import reviewsReducer from './features/reviews/reviews-slice';

// Root Reducer
export const reducer = combineReducers({
  [NameSpace.App]: appReducer,
  [NameSpace.Auth]: authReducer,
  [NameSpace.Profile]: profileReducer,
  [NameSpace.Films]: filmsReducer,
  [NameSpace.Promo]: promoReducer,
  [NameSpace.Genre]: genreReducer,
  [NameSpace.Reviews]: reviewsReducer,
});
