// Core
import cn from 'classnames';
import { memo } from 'react';

// Others
import { PosterProps } from './poster.props';

// Component
export const Poster = memo(({
  posterImage,
  className,
  ...rest
}: PosterProps): JSX.Element => (
  <div {...rest} className={cn('film-card__poster', className)}>
    <img
      src={posterImage}
      alt=""
      width="218"
      height="327"
    />
  </div>
));

Poster.displayName = 'Poster<Memo>';
