// Types
type Procedure = (...args: never[]) => void;

// Utils
export function throttle<T extends Procedure>(fn: T, delay: number) {
  let wait = false;

  return (...args: Parameters<T>) => {
    if (!wait) {
      wait = true;
      setTimeout(() => {
        fn(...args);
        wait = false;
      }, delay);
    }
  };
}
