// Core
import { camelKeys } from 'js-convert-case';
import { useEffect, useState } from 'react';
import { APIRoute, RequestsStatus } from '../../consts';

// Others
import { api } from '../../store/store';
import { Film } from '../../types/films';
import { FilmsList } from '../films-list/films-list';

// Component
export const FavoritesList = () => {
  const [status, setStatus] = useState(RequestsStatus.Idle);
  const [films, setFilms] = useState<Film[]>([]);

  useEffect(() => {
    const fetchFavoritesFilm = async () => {
      setStatus(RequestsStatus.Loading);

      try {
        const response = await api.get<Film[]>(APIRoute.Favorite);

        if (response.status === 200) {
          setStatus(RequestsStatus.Success);

          const movies = response.data
            .map((movie) => camelKeys(movie) as Film);

          setFilms(movies);
        }
      } catch (error) {
        setStatus(RequestsStatus.Failed);
      }
    };

    fetchFavoritesFilm();
  }, []);

  if (status === RequestsStatus.Failed) {
    return (
      <section className="catalog">
        <h2 style={{ textAlign: 'center' }}>An error occurred while getting the list of favorite movies.</h2>
      </section>
    );
  }

  if (status === RequestsStatus.Loading) {
    return (
      <section className="catalog">
        <h2 style={{ textAlign: 'center' }}>Loading data, please wait...</h2>
      </section>
    );
  }

  if (films.length === 0) {
    return (
      <section className="catalog">
        <h2 style={{ textAlign: 'center' }}>Favorite list is empty.</h2>
      </section>
    );
  }

  return (
    <section className="catalog">
      <h2 className="catalog__title visually-hidden">Catalog</h2>

      <FilmsList films={films} />
    </section>
  );
};
