// Core
import { useParams } from 'react-router-dom';

// Others
import { movieByIdExists } from '../../store/features/films/films-selector';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { useAppDispatch } from '../../hooks/use-app-dispatch/use-app-dispatch';

// Components
import { Logo } from '../../components/logo/logo';
import { NotFoundPage } from '../not-found-page/not-found-page';
import { FilmsSimilar } from '../../components/films-similar/films-similar';
import { Footer } from '../../components/footer/footer';
import { FilmSection } from '../../components/film-section/film-section';
import { fetchReviewsById } from '../../store/features/reviews/reviews-api';

// Component
export const FilmPage = () => {
  const params = useParams();
  const filmId = params.id ? parseInt(params.id, 10) : -1;
  const dispatch = useAppDispatch();

  dispatch(fetchReviewsById(filmId));

  const movie = useAppSelector(
    (state) => movieByIdExists(state, filmId)
  );

  if (movie) {
    return <NotFoundPage />;
  }

  return (
    <>
      <FilmSection filmId={filmId} />

      <div className="page-content">
        <FilmsSimilar id={filmId} />

        <Footer>
          <Logo light />
        </Footer>
      </div>
    </>
  );
};
