// Core
import { useState, useEffect, MouseEvent } from 'react';

// Hook
export const useShowMore = (maxCount: number, totalCount: number) => {
  const [maxShowFilms, setMaxShowFilms] = useState(maxCount);
  const [showMoreToDisplay, setShowMoreToDisplay] = useState(false);

  useEffect(() => {
    const result = (maxShowFilms <= totalCount);

    setShowMoreToDisplay(result);
  }, [maxShowFilms, totalCount]);

  const onShowMoreButton = (event: MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();

    setMaxShowFilms(maxShowFilms + maxCount);
  };

  return {
    maxShowFilms,
    showMoreToDisplay,
    onShowMoreButton
  };
};
