export const evaluationDescription = (rating: number) => {
  const value = parseInt(rating.toFixed(), 10);

  switch (value) {
    case 10:
      return 'Awesome';
    case 9:
    case 8:
      return 'Very Good';
    case 7:
    case 6:
      return 'Good';
    case 5:
    case 4:
      return 'Normal';
    default:
      return 'Bad';
  }
};
