// Core
import { createAsyncThunk } from '@reduxjs/toolkit';
import { AxiosInstance } from 'axios';
import { APIRoute } from '../../../consts';

// Others
import {
  ReviewId,
  Reviews,
  ReviewPayload
} from '../../../types/reviews';

// Async Actions


export const fetchReviewsById = createAsyncThunk<ReviewPayload, ReviewId, {
  extra: AxiosInstance,
}>(
  'data/fetchReviewsById',
  async (id: ReviewId, { rejectWithValue, extra: api }) => {
    try {
      const { data } = await api.get<Reviews[]>(`${APIRoute.Comments}/${id}`);
      return { data, id };
    } catch (error) {
      return rejectWithValue('Failed to get reviews.');
    }
  },
);
