// Core
import { memo } from 'react';
import { Link } from 'react-router-dom';

// Others
import { PlayButtonProps } from './play-button.props';

// Component
export const PlayButton = memo((props: PlayButtonProps) => {
  const { filmId } = props;

  return (
    <Link
      to={`/player/${filmId}`}
      className="btn btn--play film-card__button"
    >
      <svg viewBox="0 0 19 19" width="19" height="19">
        <use xlinkHref="#play-s"></use>
      </svg>
      <span>Play</span>
    </Link>
  );
});

PlayButton.displayName = 'PlayButton<Memo>';
