// Core
import { createAsyncThunk } from '@reduxjs/toolkit';
import { AxiosInstance } from 'axios';

// Others
import { APIRoute, NameSpace } from '../../../consts';
import { Film } from '../../../types/films';
import { RootState } from '../../store';
import { changePromoFavorite } from '../promo/promo-slice';

// Async Actions

export const fetchAllFilmsActionAsync = createAsyncThunk<void, undefined, {
  extra: AxiosInstance;
}>(
  'data/fetchFilms',
  async (_, { extra: api }) => {
    const { data } = await api.get(APIRoute.Films);
    return data;
  },
);

export const changeFavoriteFilmStatus = createAsyncThunk<
  {
    status: number;
    filmId: number;
  } | undefined,
  number,
  {
    extra: AxiosInstance,
  }
>(
  'film/changeFavoriteFilmStatus',
  async (filmId, { dispatch, rejectWithValue, getState, extra: api }) => {
    try {
      const state = getState() as RootState;
      const promo = state[NameSpace.Promo].entities;
      const movie = state[NameSpace.Films].entities.find((it) => it.id === filmId) as Film;
      const status = movie.isFavorite ? 0 : 1;

      if (promo.id === filmId) {
        dispatch(changePromoFavorite(!promo.isFavorite));
      }

      const response = await api.post<Film>(`${APIRoute.Favorite}/${filmId}/${status}`);
      if (response.status === 200) {
        return { status, filmId };
      }
    } catch (error) {
      return rejectWithValue('Failed to change favorite movie status.');
    }
  },
);
