// Core
import { camelKeys } from 'js-convert-case';
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

// Others
import { NameSpace, RequestsStatus } from '../../../consts';
import { Film } from '../../../types/films';
import { fetchPromoActionAsync } from './promo-api';
import { PromoState } from './promo-types';

// State
const initialState: PromoState = {
  status: RequestsStatus.Idle,
  entities: {} as Film,
  error: '',
};

// Slice
const promoSlice = createSlice({
  name: NameSpace.Promo,
  initialState,
  reducers: {
    changePromoFavorite(state: PromoState, action: PayloadAction<boolean>) {
      state.entities.isFavorite = action.payload;
    }
  },
  extraReducers(builder) {
    builder
      .addCase(fetchPromoActionAsync.pending, (state: PromoState) => {
        state.status = RequestsStatus.Loading;
        state.error = '';
      })
      .addCase(fetchPromoActionAsync.fulfilled, (state: PromoState, action: PayloadAction<Film>) => {
        state.status = RequestsStatus.Success;
        state.entities = camelKeys(action.payload) as Film;
      })
      .addCase(fetchPromoActionAsync.rejected, (state: PromoState, action) => {
        state.status = RequestsStatus.Failed;
        state.error = action.error.message ?? '';
      });
  },
});

// Exports
export const { changePromoFavorite } = promoSlice.actions;
export default promoSlice.reducer;
