// Core
import { ChangeEvent, FormEvent, useState } from 'react';

// Others
import { REG_EXP_EMAIL } from '../../consts';
import { loginActionAsync } from '../../store/features/auth/auth-api';
import { useAppDispatch } from '../use-app-dispatch/use-app-dispatch';
import { UseSignInReturn } from './use-sign-in.types';

// Hook
export const useSignIn = (): UseSignInReturn => {
  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const [errors, setErrors] = useState<string[]>([]);

  const dispatch = useAppDispatch();

  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    setErrors(errors.splice(0));

    if (email === '' || password === '') {
      setErrors(() => [...errors, 'Fill in all the fields.']);
      return void 0;
    }

    if (!REG_EXP_EMAIL.test(email)) {
      setErrors(() => [...errors, 'Please enter a valid email address.']);
      return void 0;
    }

    dispatch(loginActionAsync({ email, password }));
  };

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;

    if (name === 'email') {
      setEmail(value);
      return void 0;
    }

    if (name === 'password') {
      setPassword(value);
      return void 0;
    }
  };

  return [email, password, errors, handleSubmit, handleChange];
};
