// Core
import { useParams } from 'react-router-dom';

// Others
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { selectFilmById } from '../../store/features/films/films-selector';
import { RootState } from '../../store/store';

// Components
import { Logo } from '../../components/logo/logo';
import { UserBlock } from '../../components/user-block/user-block';
import { AddReviewForm } from '../../components/add-review-form/add-review-form';
import { Breadcrumbs } from '../../components/breadcrumbs/breadcrumbs';
import { Header } from '../../components/header/header';
import { Poster } from '../../components/poster/poster';

// Component
export const AddReviewPage = () => {
  const { id } = useParams();

  const movie = useAppSelector(
    (state: RootState) => selectFilmById(state, id ?? '-1'),
  );

  return (
    <section className="film-card film-card--full">
      <div className="film-card__header">
        <div className="film-card__bg">
          <img src={movie?.backgroundImage} alt="" />
        </div>

        <h1 className="visually-hidden">WTW</h1>

        <Header>
          <Logo />
          <Breadcrumbs
            filmId={movie?.id ?? 0}
            filmName={movie?.name ?? ''}
          />
          <UserBlock />
        </Header>

        <Poster
          posterImage={movie?.posterImage ?? ''}
          className='film-card__poster--small'
        />
      </div>

      <AddReviewForm id={id ?? '-1'} />
    </section>
  );
};
