// Others
import { RootState } from '../../store';
import { NameSpace, RequestsStatus } from '../../../consts';
import { Reviews } from '../../../types/reviews';

// Selectors

export const selectCurrentReviewId = (state: RootState): number =>
  state[NameSpace.Reviews].currentId;

export const selectReviewsList = (state: RootState): Reviews[] =>
  state[NameSpace.Reviews].entities;

export const selectIsErrorReviews = (state: RootState): boolean =>
  state[NameSpace.Reviews].status === RequestsStatus.Failed;
