// Core
import { memo, RefObject, useEffect, useState } from 'react';

// Others
import { formatTime } from '../../utils/format-time';
import { throttle } from '../../utils/throttle';

// Props
type VideoProgressProps = {
  videoRef: RefObject<HTMLVideoElement>;
}

// Component
const VideoProgressComponent = ({ videoRef }: VideoProgressProps): JSX.Element => {
  // States
  const [duration, setDuration] = useState<number>(0);
  const [progress, setProgress] = useState<number>(0);

  // Effects
  useEffect(() => {
    const videoPlayer = videoRef.current;

    if (videoPlayer) {
      videoPlayer.ontimeupdate = throttle(() => {
        setProgress(
          (Math.floor(videoPlayer.currentTime) * 100) / Math.floor(videoPlayer.duration)
        );
        setDuration(videoPlayer.duration - videoPlayer.currentTime);
      }, 1000);
    }

    return () => {
      if (videoPlayer) {
        videoPlayer.ontimeupdate = null;
      }
    };
  }, [videoRef]);

  // Render
  // --------------------------------------------------------------

  const { h, m, s } = formatTime(duration);

  return (
    <div className="player__controls-row">
      <div className="player__time">
        <progress className="player__progress" value={progress} max="100" />
        <div className="player__toggler" style={{ left: `${progress}%` }}>
          Toggler
        </div>
      </div>

      <div className="player__time-value">
        {h === '00' ? `-${m}:${s}` : `-${h}:${m}:${s}`}
      </div>
    </div>
  );
};

export const VideoProgress = memo(VideoProgressComponent);
