// Core
import React from 'react';

// Others
import { ReviewCardProps } from './review-card.props';

// Component
export const ReviewCard = (props: ReviewCardProps) => {
  const { review } = props;

  return (
    <div className="review">
      <blockquote className="review__quote">
        <p className="review__text">{review.comment}</p>
        <footer className="review__details">
          <cite className="review__author">{review.user.name}</cite>
          <time
            className="review__date"
            dateTime={new Intl.DateTimeFormat('en-CA', { year: 'numeric', month: '2-digit', day: '2-digit' }).format(new Date(review.date))}
          >
            {new Intl.DateTimeFormat('en', { month: 'long', year: 'numeric', day: '2-digit' }).format(new Date(review.date))}
          </time>
        </footer>
      </blockquote>
      <div className="review__rating">{review.rating.toFixed(1)}</div>
    </div>
  );
};
