// Core
import { useEffect, useState } from 'react';
import { camelKeys } from 'js-convert-case';

// Others
import { api } from '../../store/store';
import { FilmsSimilarProps } from './films-similar.props';
import { FilmsList } from '../films-list/films-list';
import { Film } from '../../types/films';

// Component
export const FilmsSimilar = (props: FilmsSimilarProps): JSX.Element => {
  const { id } = props;

  const [similars, setSimilars] = useState<[] | Film[]>([]);

  useEffect(() => {
    const fetchSimilarFilms = async (filmId: number) => {
      const { data } = await api.get<Film[]>(`/films/${filmId}/similar`);
      const result = data.map((it) => camelKeys(it)) as Film[];
      setSimilars(result);
    };

    fetchSimilarFilms(id);
  }, [id]);

  return (
    <section className="catalog catalog--like-this">
      <h2 className="catalog__title">More like this</h2>

      <FilmsList films={similars.slice(0, 4)} />
    </section>
  );
};
