// Others
import { Store } from './store/store';
import { fetchAllFilmsActionAsync } from './store/features/films/films-api';
import { checkAuthStatus } from './store/features/auth/auth-api';
import { fetchPromoActionAsync } from './store/features/promo/promo-api';
import { changeAppInitializationStatus } from './store/features/app/app-slice';

// Initilize
export const initialize = (store: Store) => {
  Promise.all([
    store.dispatch(checkAuthStatus()),
    store.dispatch(fetchAllFilmsActionAsync()),
    store.dispatch(fetchPromoActionAsync()),
  ]).then((data) => {
    store.dispatch(changeAppInitializationStatus(true));
  }).catch((error) => {
    // TODO: create error handling
  });
};
