// Core
import { ChangeEvent, FormEvent, useState } from 'react';
import { useNavigate } from 'react-router-dom';

// Others
import { ReviewRating } from '../review-rating/review-rating';
import { AddReview } from '../../types/reviews';
import { api } from '../../store/store';
import { AddReviewFormProps } from './add-review-form.props';
import { RequestsStatus } from '../../consts';
import { useAppDispatch } from '../../hooks/use-app-dispatch/use-app-dispatch';
import { fetchReviewsById } from '../../store/features/reviews/reviews-api';

// Component
export const AddReviewForm = (props: AddReviewFormProps): JSX.Element => {
  const { id } = props;

  const [status, setStatus] = useState(RequestsStatus.Idle);
  const [message, setMessage] = useState('');
  const [rating, setRating] = useState('0');
  const [subBtnDisabled, setSubBtnDisabled] = useState(true);

  const dispatch = useAppDispatch();

  const navigate = useNavigate();

  const validate = () => {
    setSubBtnDisabled(!(message.length >= 50 && rating !== '0'));
  };

  const handleMessageChange = (event: ChangeEvent<HTMLTextAreaElement>) => {
    const { value } = event.target;
    validate();
    setMessage(value);
  };

  const handleRatingChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { value } = event.target;
    validate();
    setRating(value);
  };

  const handleSubmit = (event: FormEvent) => {
    event.preventDefault();
    createNewReview({ rating, comment: message });
  };

  const createNewReview = async (data: AddReview) => {
    setStatus(RequestsStatus.Loading);

    try {
      const result = await api.post(`/comments/${id}`, data);

      if (result.status === 200) {
        setStatus(RequestsStatus.Success);
        navigate(`/films/${id}`);
        dispatch(fetchReviewsById(parseInt(id, 10)));
      }
    } catch (error) {
      setStatus(RequestsStatus.Failed);
    }
  };

  return (
    <div className="add-review">
      {status === RequestsStatus.Failed && (
        <p
          className="film-card__text"
          style={{
            color: 'red',
            width: 'auto',
            textAlign: 'center',
          }}
        >
          An error occurred while submitting your review.
        </p>
      )}

      <form onSubmit={handleSubmit} action="#" className="add-review__form">
        <ReviewRating
          ratingValue={rating}
          onChangeRating={handleRatingChange}
        />
        <div className="add-review__text">
          <textarea
            minLength={50}
            maxLength={400}
            value={message}
            onChange={handleMessageChange}
            className="add-review__textarea"
            name="review-text"
            id="review-text"
            placeholder="Review text"
          />

          <div className="add-review__submit">
            <button
              className="add-review__btn"
              type="submit"
              disabled={subBtnDisabled}
            >
              Post
            </button>
          </div>

        </div>
      </form>
    </div>
  );
};
