// Core
import {
  createSlice,
  PayloadAction,
} from '@reduxjs/toolkit';

import { camelKeys } from 'js-convert-case';

// Others
import { Film } from '../../../types/films';
import { NameSpace, RequestsStatus } from '../../../consts';
import { changeFavoriteFilmStatus, fetchAllFilmsActionAsync } from './films-api';
import { FilmsState } from './films-types';

// State
const initialState: FilmsState = {
  entities: [],
  error: null,
  status: RequestsStatus.Idle,
};

// Slice
const filmsSlice = createSlice({
  name: NameSpace.Films,
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(
        fetchAllFilmsActionAsync.pending.type,
        (state: FilmsState) => {
          state.status = RequestsStatus.Loading;
          state.entities = [];
          state.error = null;
        }
      )
      .addCase(
        fetchAllFilmsActionAsync.rejected.type,
        (state: FilmsState,
          { payload }: PayloadAction<string>) => {
          state.status = RequestsStatus.Failed;
          state.error = payload;
        }
      )
      .addCase(
        fetchAllFilmsActionAsync.fulfilled.type,
        (state: FilmsState, { payload }: PayloadAction<Film[]>) => {
          state.status = RequestsStatus.Success;
          state.entities = payload.map((it) => camelKeys(it)) as Film[];
        }
      )
      .addCase(
        changeFavoriteFilmStatus.fulfilled.type,
        (
          state: FilmsState,
          action: PayloadAction<{ status: number, filmId: number }>,
        ) => {
          const { filmId, status } = action.payload;

          const index = state.entities
            .findIndex((it) => it.id === filmId);

          state.entities[index].isFavorite = !!status;
        }
      );
  },
});

export default filmsSlice.reducer;
