// Core
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

// Others
import { NameSpace, RequestsStatus } from '../../../consts';
import { ReviewPayload } from '../../../types/reviews';
import { fetchReviewsById } from './reviews-api';
import { ReviewsState } from './reviews-types';

// State
const initialState: ReviewsState = {
  status: RequestsStatus.Idle,
  entities: [],
  currentId: -1,
  error: '',
};

// Slice
export const reviewsSlice = createSlice({
  name: NameSpace.Reviews,
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(fetchReviewsById.pending, (state: ReviewsState) => {
        state.status = RequestsStatus.Loading;
        state.error = '';
      })
      .addCase(fetchReviewsById.fulfilled, (state: ReviewsState, action: PayloadAction<ReviewPayload>) => {
        state.status = RequestsStatus.Success;
        state.entities = [];
        state.entities = action.payload.data;
        state.currentId = action.payload.id;
      })
      .addCase(fetchReviewsById.rejected, (state: ReviewsState, action) => {
        state.status = RequestsStatus.Failed;
        state.error = action.error.message || '';
      });
  },
});

// Exports
export default reviewsSlice.reducer;
