// Core
import { Routes, Route } from 'react-router-dom';
import { Bars } from 'react-loader-spinner';

// Others
import { AppRoute } from '../../consts';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { getAppInitializationStatus } from '../../store/features/app/app-selector';
import { browserHistory } from '../../browser-history';

// Components
import { PrivateRoute } from '../private-route/private-route';
import { HistoryRouter } from '../history-route/history-route';
import { NotFoundPage } from '../../pages/not-found-page/not-found-page';
import { MainPage } from '../../pages/main-page/main-page';
import { SignInPage } from '../../pages/sign-in-page/sign-in-page';
import { FilmPage } from '../../pages/film-page/film-page';
import { PlayerPage } from '../../pages/player-page/player-page';
import { FavoritesPage } from '../../pages/favorites-page/favorites-page';
import { AddReviewPage } from '../../pages/add-review-page/add-review-page';
import { EmptyWrapper } from '../empty-wrapper/empty-wrapper';

// Component
export const App = (): JSX.Element => {
  const isInitialized = useAppSelector(getAppInitializationStatus);

  if (!isInitialized) {
    return (
      <HistoryRouter history={browserHistory}>
        <EmptyWrapper>
          <h1 style={{ fontSize: '24px' }}>
            Application initializing, please wait.
          </h1>

          <Bars
            height="45"
            width="45"
            color="#c9b37e"
            ariaLabel="bars-loading"
            wrapperStyle={{
              justifyContent: 'center',
              marginTop: '45px'
            }}
            wrapperClass=""
          />
        </EmptyWrapper>
      </HistoryRouter>
    );
  }

  return (
    <HistoryRouter history={browserHistory}>
      <Routes>
        <Route
          path={AppRoute.Root}
          element={<MainPage />}
        />
        <Route
          path={AppRoute.Login}
          element={<SignInPage />}
        />
        <Route
          path={AppRoute.Films}
          element={<FilmPage />}
        />
        <Route
          path={AppRoute.Player}
          element={<PlayerPage />}
        />
        <Route
          path={AppRoute.AddReview}
          element={
            <PrivateRoute>
              <AddReviewPage />
            </PrivateRoute>
          }
        />
        <Route
          path={AppRoute.Favorites}
          element={
            <PrivateRoute>
              <FavoritesPage />
            </PrivateRoute>
          }
        />
        <Route path='*' element={<NotFoundPage />} />
      </Routes>
    </HistoryRouter>
  );
};
