// Others
import { useAppDispatch } from '../../hooks/use-app-dispatch/use-app-dispatch';
import { changeFavoriteFilmStatus } from '../../store/features/films/films-api';
import { MyListButtonProps } from './my-list-button.props';

// Component
export const MyListButton = (props: MyListButtonProps) => {
  const { filmId, isFavorite } = props;

  const dispatch = useAppDispatch();

  const handleButtonClick = () => {
    dispatch(changeFavoriteFilmStatus(filmId));
  };

  return (
    <button
      onClick={handleButtonClick}
      className="btn btn--list film-card__button"
      type="button"
    >
      <svg viewBox="0 0 19 20" width="19" height="20">
        <use xlinkHref={isFavorite ? '#in-list' : '#add'} />
      </svg>
      <span>My list</span>
    </button>
  );
};
