// Others
import { TabOverviewProps } from './tab-overview.props';
import { evaluationDescription } from '../../utils/evaluation-description';

// Component
export const TabOverview = ({ details }: TabOverviewProps): JSX.Element => {
  const {
    description,
    rating,
    director,
    scoresCount,
    starring,
  } = details;

  const ratingText = evaluationDescription(rating);

  return (
    <>
      <div className="film-rating">
        <div className="film-rating__score">{rating.toFixed(1)}</div>
        <p className="film-rating__meta">
          <span className="film-rating__level">{ratingText}</span>
          <span className="film-rating__count">{scoresCount} ratings</span>
        </p>
      </div>

      <div className="film-card__text">
        <p>{description}</p>

        <p className="film-card__director"><strong>Director: {director}</strong></p>

        <p className="film-card__starring">
          <strong>Starring: {
            starring.map((it) => it).join(', ')
          } and other
          </strong>
        </p>
      </div>
    </>
  );
};
