// Core
import { MouseEvent } from 'react';
import { Link } from 'react-router-dom';
import cn from 'classnames';

// Others
import { TabsList } from '../../consts';
import { TabsProps } from './tabs.props';

// Component
export const Tabs = (props: TabsProps): JSX.Element => {
  const { currentTab, onChangeCurrentTab } = props;

  const handleClickLink = (event: MouseEvent<HTMLAnchorElement>) => {
    event.preventDefault();
    const { tab } = ((event.target) as HTMLAnchorElement).dataset;

    onChangeCurrentTab(tab as TabsList);
  };

  return (
    <nav className="film-nav film-card__nav">
      <ul className="film-nav__list">
        <li
          className={cn('film-nav__item', {
            'film-nav__item--active': currentTab === TabsList.Overview,
          })}
        >
          <Link
            data-tab={TabsList.Overview}
            onClick={handleClickLink}
            to='#overview'
            className="film-nav__link"
          >
            Overview
          </Link>
        </li>
        <li
          className={cn('film-nav__item', {
            'film-nav__item--active': currentTab === TabsList.Details,
          })}
        >
          <Link
            data-tab={TabsList.Details}
            onClick={handleClickLink}
            to="#details"
            className="film-nav__link"
          >
            Details
          </Link>
        </li>
        <li
          className={cn('film-nav__item', {
            'film-nav__item--active': currentTab === TabsList.Reviews,
          })}
        >
          <Link
            data-tab={TabsList.Reviews}
            onClick={handleClickLink}
            to="#reviews"
            className="film-nav__link"
          >
            Reviews
          </Link>
        </li>
      </ul>
    </nav>
  );
};
