// Others
import { useSignIn } from '../../hooks/use-sign-in/use-sign-in';

// Component
export const SignInForm = () => {
  const [
    email,
    password,
    errors,
    handleSubmit,
    handleChange,
  ] = useSignIn();

  return (
    <form onSubmit={handleSubmit} action="#" className="sign-in__form">
      <div className="sign-in__message">{errors.map((err) => (
        <p key={err}>{err}</p>
      ))}
      </div>
      <div className="sign-in__fields">
        <div className="sign-in__field">
          <input
            value={email}
            onChange={handleChange}
            className="sign-in__input"
            type="email"
            placeholder="Email address"
            name="email"
            id="user-email"
          />
          <label className="sign-in__label visually-hidden" htmlFor="email">Email address</label>
        </div>
        <div className="sign-in__field">
          <input
            value={password}
            onChange={handleChange}
            className="sign-in__input"
            type="password"
            placeholder="Password"
            name="password"
            id="user-password"
          />
          <label className="sign-in__label visually-hidden" htmlFor="password">Password</label>
        </div>
      </div>
      <div className="sign-in__submit">
        <button className="sign-in__btn" type="submit">Sign in</button>
      </div>
    </form>
  );
};
