export function formatTime(duration: number) {
  const hrs = ~~(duration / 3600);
  const mins = ~~((duration % 3600) / 60);
  const secs = ~~duration % 60;

  const h = hrs < 10 ? `0${hrs}` : `${hrs}`;
  const m = mins < 10 ? `0${mins}` : `${mins}`;
  const s = secs < 10 ? `0${secs}` : `${secs}`;

  return { h, m, s };
}
