// Core
import { useState } from 'react';

// Others
import { TabsList } from '../../consts';
import { TabProps } from './tab.props';
import { RootState } from '../../store/store';
import { Film } from '../../types/films';
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';

// Components
import { Tabs } from '../tabs/tabs';
import { TabOverview } from '../tab-overview/tab-overview';
import { TabDetails } from '../tab-details/tab-details';
import { TabComments } from '../tab-comments/tab-comments';
import { selectFilmById } from '../../store/features/films/films-selector';
import { selectReviewsList } from '../../store/features/reviews/reviews-selector';

// Component
export const Tab = ({ filmId }: TabProps) => {
  const [currentTab, setCurrentTab] = useState(TabsList.Overview);

  const movie = useAppSelector(
    (state: RootState) => selectFilmById(state, filmId.toString()) as Film
  );

  const reviews = useAppSelector(selectReviewsList);

  return (
    <div className="film-card__desc">
      <Tabs currentTab={currentTab} onChangeCurrentTab={setCurrentTab} />

      {currentTab === TabsList.Overview && (
        <TabOverview
          details={{
            description: movie.description,
            scoresCount: movie.scoresCount,
            rating: movie.rating,
            director: movie.director,
            starring: movie.starring,
          }}
        />
      )}

      {currentTab === TabsList.Details && (
        <TabDetails
          details={{
            director: movie.director,
            genre: movie.genre,
            released: movie.released,
            starring: movie.starring,
            runTime: movie.runTime,
          }}
        />
      )}

      {currentTab === TabsList.Reviews && (
        <TabComments
          reviews={reviews}
        />
      )}

    </div>
  );
};
