// Core
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

// Others
import { Genres, NameSpace } from '../../../consts';
import { SelectedGenreState } from './genres-types';

// State
const initialState: SelectedGenreState = {
  selectedGenre: Genres.AllGenres,
};

// Slice
const selectedGenreSlice = createSlice({
  name: NameSpace.Genre,
  initialState,
  reducers: {
    changeSelectedGenre(
      state: SelectedGenreState,
      { payload }: PayloadAction<Genres>
    ) {
      state.selectedGenre = payload;
    }
  },
});

// Exports
export const { changeSelectedGenre } = selectedGenreSlice.actions;
export default selectedGenreSlice.reducer;
