// Core
import { createSlice } from '@reduxjs/toolkit';

// Others
import { AuthorizationStatus, NameSpace } from '../../../consts';
import { checkAuthStatus, loginActionAsync, logoutActionAsync } from './auth-api';
import { AuthState } from './auth-types';

// State
const initialState: AuthState = {
  authorizationStatus: AuthorizationStatus.Unknown,
};

// Slice
const authSlice = createSlice({
  name: NameSpace.Auth,
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(checkAuthStatus.fulfilled, (state: AuthState) => {
        state.authorizationStatus = AuthorizationStatus.Auth;
      })
      .addCase(checkAuthStatus.rejected, (state: AuthState) => {
        state.authorizationStatus = AuthorizationStatus.NoAuth;
      })
      .addCase(loginActionAsync.fulfilled, (state: AuthState) => {
        state.authorizationStatus = AuthorizationStatus.Auth;
      })
      .addCase(loginActionAsync.rejected, (state: AuthState) => {
        state.authorizationStatus = AuthorizationStatus.NoAuth;
      })
      .addCase(logoutActionAsync.fulfilled, (state: AuthState) => {
        state.authorizationStatus = AuthorizationStatus.NoAuth;
      });
  },
});

// Exports
export default authSlice.reducer;
