// Others
import { useAppSelector } from '../../hooks/use-app-selector/use-app-selector';
import { selectAuthStatus } from '../../store/features/auth/auth-selector';
import { selectPromoFilm } from '../../store/features/promo/promo-selector';

// Components
import { Header } from '../header/header';
import { Logo } from '../logo/logo';
import { UserBlock } from '../user-block/user-block';
import { Poster } from '../poster/poster';
import { MyListButton } from '../my-list-button/my-list-button';
import { PlayButton } from '../play-button/play-button';

// Component
export const PromoSection = () => {
  const isAuthorized = useAppSelector(selectAuthStatus);
  const movie = useAppSelector(selectPromoFilm);

  return (
    <section className="film-card">
      <div className="film-card__bg">
        <img src={movie.backgroundImage} alt="" />
      </div>

      <h1 className="visually-hidden">WTW</h1>

      <Header className="film-card__head">
        <Logo />
        <UserBlock />
      </Header>

      <div className="film-card__wrap">
        <div className="film-card__info">
          <Poster posterImage={movie.posterImage} />

          <div className="film-card__desc">
            <h2 className="film-card__title">{movie.name}</h2>
            <p className="film-card__meta">
              <span className="film-card__genre">{movie.genre}</span>
              <span className="film-card__year">{movie.released}</span>
            </p>

            <div className="film-card__buttons">
              <PlayButton filmId={movie.id} />

              {isAuthorized && (
                <MyListButton filmId={movie.id} isFavorite={movie.isFavorite} />
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
