export type TToken = string;

export class Token {
  private static key = 'wtw-token';

  static get(): TToken {
    const token = localStorage.getItem(Token.key);
    return token ?? '';
  }

  static save(token: TToken): void {
    localStorage.setItem(Token.key, token);
  }

  static drop(): void {
    localStorage.removeItem(Token.key);
  }
}
