// Core
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { camelKeys } from 'js-convert-case';
import { NameSpace } from '../../../consts';

// Others
import { ProfileData } from '../../../types/profile-data';

// State
const initialState: ProfileData = {
  id: -1,
  email: '',
  name: '',
  avatarUrl: '',
  token: '',
};

// Slice
const profileSlice = createSlice({
  name: NameSpace.Profile,
  initialState,
  reducers: {
    fillProfile(state: ProfileData, action: PayloadAction<ProfileData>) {
      const data = camelKeys(action.payload) as ProfileData;

      state.id = data.id;
      state.email = data.email;
      state.name = data.name;
      state.avatarUrl = data.avatarUrl;
      state.token = data.token;
    },
    clearProfile(state: ProfileData) {
      state.id = -1;
      state.email = '';
      state.name = '';
      state.avatarUrl = '';
      state.token = '';
    },
  },
});

// Exports

export const {
  fillProfile,
  clearProfile,
} = profileSlice.actions;

export default profileSlice.reducer;
