// Core
import { createAsyncThunk } from '@reduxjs/toolkit';
import { AxiosInstance } from 'axios';

// Others
import { APIRoute, AppRoute } from '../../../consts';
import { Token } from '../../../services/token';
import { AuthData } from '../../../types/auth-data';
import { ProfileData } from '../../../types/profile-data';
import { redirectToRoute } from '../../action';
import { AppDispatch } from '../../store';
import { clearProfile, fillProfile } from '../profile/profile-slice';

// Async Actions

export const checkAuthStatus = createAsyncThunk<void, undefined, {
  dispatch: AppDispatch,
  extra: AxiosInstance,
}>(
  'auth/check',
  async (_, { dispatch, extra: api }) => {
    const response = await api.get(APIRoute.Login);

    if (response.status === 200) {
      dispatch(fillProfile(response.data));
    }

    if (response.status === 401) {
      dispatch(clearProfile());
    }
  },
);

export const loginActionAsync = createAsyncThunk<void, AuthData, {
  dispatch: AppDispatch,
  extra: AxiosInstance,
}>(
  'auth/login',
  async (credentials, { dispatch, extra: api }) => {
    const response = await api.post<ProfileData>(APIRoute.Login, credentials);

    if (response.status === 200) {
      const { token } = response.data;
      Token.save(token);

      dispatch(fillProfile(response.data));
      dispatch(redirectToRoute(AppRoute.Root));
    }
  },
);

export const logoutActionAsync = createAsyncThunk<void, undefined, {
  dispatch: AppDispatch,
  extra: AxiosInstance,
}>(
  'auth/logout',
  async (_, { dispatch, extra: api }) => {
    await api.delete(APIRoute.Logout);
    Token.drop();

    dispatch(clearProfile());
    dispatch(redirectToRoute(AppRoute.Root));

  },
);
